import React, { useState, useEffect } from 'react';
import './Header.css';
import '@fortawesome/fontawesome-free/css/all.min.css';
import logo from './img/logo.png'; // Ensure the path is correct


const Nav = () => {
  
  const [currentLocation, setCurrentLocation] = useState('H Ch Minh');
  const [isDropdownOpen, setDropdownOpen] = useState(false);
  const [locationsData, setLocationsData] = useState([]); // Khi to state  lu d liu locations

  const toggleDropdown = () => {
    setDropdownOpen(!isDropdownOpen);
  };

  const handleLocationChange = (location) => {
    setCurrentLocation(location);
    setDropdownOpen(false);
  };

  // Ly d liu locations t API
  useEffect(() => {
    const fetchLocations = async () => {
      try {
        const response = await fetch('http://localhost:5000/api/locations');
        const data = await response.json();
        setLocationsData(data); // Cp nht state vi d liu ly c
      } catch (error) {
        console.error('Error fetching locations:', error);
      }
    };

    fetchLocations();
  }, []); // Chy mt ln khi component mount
  return (
    <div id="header">
  <div className="menu">
    <div className="logo_menu">
      <img src={logo} alt="" />
    </div>
    <div id="location">
      <div className="icon_location" onClick={toggleDropdown}>
        <i className="fa-solid fa-location-dot" />
        <p>
          Gi xem ti <br /> {currentLocation}
        </p>
        <i className="fa-solid fa-chevron-down" />
      </div>

      {/* Dropdown hin th khi ngi dng nhn vo mi tn */}
      {isDropdownOpen && (
        <ul className="location_dropdown">
          {locationsData.map((location) => (
            <li
              key={location.id}
              onClick={() => handleLocationChange(location.name)}
              className={location.name === currentLocation ? 'active' : ''}
            >
              {location.name}
            </li>
          ))}
        </ul>
      )}
    </div>
    <div className="search">
      <input
        style={{ textAlign: "center" }}
        className="btn_search"
        type="search"
        placeholder="Bn cn tm sn phm g?"
      />
      <i className="fa-solid fa-magnifying-glass" />
    </div>
    <div id="Phone">
      <div className="icon_phone">
        <i
          className="fa-solid fa-phone"
          style={{ color: "white", fontSize: 25 }}
        />
      </div>
      <div className="SDT">
        <p>
          Gi mua hng <br /> 18003638
        </p>
      </div>
    </div>
    <div id="Truck">
      <div className="icon_Truck">
        <i
          className="fa-solid fa-truck-fast"
          style={{ color: "white", fontSize: 25 }}
        />
      </div>
      <div className="Check">
        <p>
          Tra cu <br /> n hng
        </p>
      </div>
    </div>
    <div id="Bag">
      <div className="icon_Bag">
        <i
          className="fa-solid fa-bag-shopping"
          style={{ color: "white", fontSize: 25 }}
        />
      </div>
      <div className="Bag_text">
        <p>Gi hng</p>
      </div>
    </div>
    <div id="user">
      <div className="icon_user">
        <i
          className="fa-solid fa-user"
          style={{ color: "white", fontSize: 20 }}
        />
        <p>ng nhp</p>
      </div>
    </div>
  </div>
</div>

  );
}

export default Nav;
